import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
  background-color: #F8F9FA;
  border-radius: 10px;
`;

const Title = styled.h2`
  font-size: 1.5rem;
  color: #2C3E50;
  margin-bottom: 1.5rem;
`;

const SelectionSummary = styled.div`
  margin-bottom: 1.5rem;
`;

const PriceDisplay = styled.div`
  font-size: 1.5rem;
  font-weight: bold;
  color: #3498DB;
  margin-bottom: 1.5rem;
`;

const NextButton = styled.button`
  background-color: #3498DB;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 5px;
  width: 100%;
`;

function BasicPriceDisplay({ selections, basicPrice, onNext }) {
  return (
    <Container>
      <Title>    </Title>
      <SelectionSummary>
        <p>: {selections.category}</p>
        <p>: {selections.item}</p>
        <p>: {selections.size}</p>
        <p>: {selections.weight}</p>
        <p>: {selections.quantity}</p>
        <p> : {selections.specialHandling}</p>
      </SelectionSummary>
      <PriceDisplay> : {basicPrice.toLocaleString()}</PriceDisplay>
      <NextButton onClick={onNext}> </NextButton>
    </Container>
  );
}

export default BasicPriceDisplay;

